<template>
  <div id="home-container">
    <h1>{{ msg }}</h1>
    <div>
      {{ newWord }}
    </div>
    <div>
      {{ this.geolection }}
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: 'Welcome to Vue.js App!',
      newWord: 'Something',
      geolection: 0
    }
  },
  methods: {
    geolocation () {
      console.log(navigator.geolocation.getCurrentPosition())
      this.geolection = navigator.geolocation.getCurrentPosition()
    }
  },
  mounted () {
    this.geolocation()
  }
}
</script>

<style scoped>
#home-container{
  height: 100%;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



